




















































.listing {
  display: flex;
  flex-wrap: wrap;
  padding: 1.5rem;
  justify-content: center;
  align-items: flex-start;
  margin: 0 auto;
  max-width: 1648px;
}
.mesh {
  margin: 1.5rem;
  border: 2px solid #111;
  width: 350px;
  .meta {
    display: flex;
    align-items: center;
    color: inherit;
    text-decoration: none;
    border-top: 2px solid #111;
    overflow: hidden;
    .title, .creator {
      white-space: nowrap;
    }
    .title {
      flex-grow: 1;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding-left: 0.75rem;
    }
    .creator {
      font-size: 0.75em;
      padding: 0 0.75rem 0 0.5rem;
    }
  }
}
