

















































































.frames {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  padding: 1rem 0 1rem 1rem;
  overflow-y: scroll;
  img {
    width: 100%;
    vertical-align: middle;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    image-rendering: pixelated;
    cursor: pointer;
    opacity: 0.5;
    transition: opacity ease-out .15s;
    will-change: opacity;
    &.active {
      opacity: 1;
      cursor: default;
    }
  }
}
