










































































.pagination {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  > a, > span {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    padding: 0 1rem;
    margin: 0 0.5rem;
    color: inherit;
    text-decoration: none;
    outline: 0;
    cursor: default;
    background-color: #222;
    border: 2px solid #111;
  }
  > a {
    cursor: pointer;
    transition: background-color ease-out .15s, opacity ease-out .15s;
    will-change: background-color, opacity;
    &.active, &:hover {
      background-color: #333;
    }
    &.disabled {
      opacity: .5;
      cursor: default;
      &:hover {
        background-color: #222;
      }
    }
  }
}
