























































































































































.auth {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .5);
  > form {
    background: #222;
    padding: 2rem;
    border: 2px solid #111;
  }
}
.field {
  > label {
    display: block;
    > span {
      color: #999;
    }
  }
  > input {
    font-size: 1.5em;
    font-family: inherit;
    color: inherit;
    margin: 0 0 0.25rem;
    padding: 0 1.5rem;
    height: 50px;
    background: transparent;
    border: 2px solid #111;
    outline: 0;
  }
}
.submit, .alternative {
  text-align: center;
}
.submit {
  margin: 1rem 0;
  > button {
    font-size: 1.5em;
    font-family: inherit;
    color: inherit;
    padding: 0 1.5rem;
    height: 60px;
    background-color: transparent;
    border: 2px solid #111;
    outline: 0;
    cursor: pointer;
    transition: background-color ease-out .15s;
    will-change: background-color;
    &:hover {
      background-color: #333;
    }
    &[disabled] {
      opacity: .75;
      cursor: default;
    }
  }
}
.error {
  margin: 1rem 0;
  background-color: #822;
  text-align: center;
}
.alternative {
  border-top: 1px solid #111;
  margin: 2rem 0 0;
  padding: 2rem 0 0;
  text-align: center;
  > a {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
    &:hover {
      color: #fff;
    }
  }
}
