





































































.header {
  display: flex;
  justify-content: center;
  background: #222;
  border-bottom: 2px solid #111;
  > div {
    max-width: 1600px;
    padding: 0 1.75rem;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    > div {
      display: flex;
      align-items: center;
    }
  }
}
.nav > a {
  display: flex;
  align-items: center;
  height: 100%;
  color: inherit;
  text-decoration: none;
  outline: 0;
}
.actions {
  > a, > button {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    display: flex;
    align-items: center;
    height: 100%;
    margin: 0;
    padding: 0 1.5rem;
    border: 0;
    outline: 0;
    cursor: pointer;
    text-decoration: none;
    background-color: transparent;
    transition: background-color ease-out .15s;
    will-change: background-color;
    border-left: 2px solid #111;
    &:last-child {
      border-right: 2px solid #111;
    }
    &:hover {
      background-color: #333;
    }
  }
}
.dropdown {
  position: relative;
  height: 100%;
  .toggle {
    display: flex;
    min-width: 120px;
    height: 100%;
    padding: 0 1.5rem;
    justify-content: space-between;
    align-items: center;
    transition: background-color ease-out .15s;
    will-change: background-color;
    border-left: 2px solid #111;
    > i {
      display: block;
      margin-left: 1rem;
      font-style: normal;
      transform: translate(-50%, 0) rotate(90deg);
      transition: transform ease-out .15s;
      will-change: transform;
    }
  }
  &:last-child .toggle {
    border-right: 2px solid #111;
  }
  .content {
    position: absolute;
    top: 100%;
    right: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: #222;
    max-height: 0;
    transition: max-height ease-out .15s;
    will-change: max-height;
    overflow: hidden;
    border: 2px solid #111;
    border-top: 0;
    > a {
      padding: 0.5rem 1rem;
      color: inherit;
      text-decoration: none;
      outline: 0;
      cursor: pointer;
      border-top: 2px solid #111;
      transition: background-color ease-out .15s;
      will-change: background-color;
      &:hover {
        background-color: #333;
      }
    }
  }
  &:hover {
    .toggle {
      background-color: #333;
      > i {
        transform: translate(-50%, 0) rotate(-90deg);
      }
    }
    .content {
      max-height: 6rem;
    }
  }
}
