













































































































































































































































































































































.editor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0 2rem;
}
.title {
  font-size: 2.5em;
  margin: 2rem auto 1rem;
  > input {
    width: 500px;
    margin: 0;
    padding: 0 1.5rem;
    font-family: inherit;
    color: inherit;
    font-size: inherit;
    height: 50px;
    background-color: #222;
    border: 2px solid #111;
    outline: 0;
  }
  > small {
    font-size: 0.75em;
    > a {
      color: inherit;
      text-decoration: none;
      outline: 0;
    }
  }
}
.meta {
  margin: 0 auto 1.5rem;
  font-size: 0.75em;
}
.wrapper, .toolbar {
  max-width: 1400px;
  width: 100%;
}
.wrapper {
  display: flex;
  border-left: 2px solid #111;
  border-right: 2px solid #111;
  > div {
    width: calc(50% - 100px);
    &.frames {
      position: relative;
      width: 200px;
    }
  }
}
.toolbar {
  display: flex;
  background: #222;
  font-size: 1.25em;
  border-top: 2px solid #111;
  border-bottom: 2px solid #111;
  > div {
    display: flex;
    align-items: center;
    width: (100% / 3);
    &:nth-child(2) {
      justify-content: center;
    }
    &:nth-child(3) {
      justify-content: flex-end;
    }
  }
  button, input, label {
    margin: 0;
    padding: 0 1.5rem;
    font-family: inherit;
    font-size: inherit;
    height: 50px;
    background-color: #222;
    color: inherit;
    border: 0;
    outline: 0;
    border-left: 2px solid #111;
    &:last-child {
      border-right: 2px solid #111;
    }
  }
  button {
    cursor: pointer;
    transition: background-color ease-out .15s, opacity ease-out .15s;
    will-change: background-color, opacity;
    &.active, &:hover {
      background-color: #333;
    }
    &.warning {
      background-color: #822;
    }
    &[disabled] {
      opacity: .5;
      cursor: default;
      &:hover {
        background-color: #222;
      }
    }
  }
  input {
    &[type=color] {
      width: 5rem;
      padding: 0 0.25rem;
      cursor: pointer;
    }
    &[type=number] {
      width: 6rem;
      border-left: 0;
      &::-webkit-inner-spin-button {
        opacity: 1;
      }
    }
  }
  label {
    display: flex;
    align-items: center;
    padding-right: 0;
  }
}
